import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import "./agent.css"
import scrollreveal from "scrollreveal";
const AgentView = () => {
  useEffect(() => {
    const sr = scrollreveal({
      origin: "right",
      distance: "80px",
      duration: 2000,
      reset: false,
    });
    sr.reveal(
      `
      container py-4
    `,
      {
        opacity: 0,
        interval: 100,
      }
    );
  }, []);
  const jwt =JSON.parse(localStorage.getItem('jwt'));
  const summaryw=JSON.parse(localStorage.getItem('summary'));
  const [user, setUser] = useState({
    firstName: "",
    username: "",
    licenceNumber: "",
    businessLNum: "",
    companyType: ""
  });
  const { id } = useParams();
  useEffect(() => {
    loadUser();
  }, []);
  const loadUser = async () => {
    
    const res = await axios.get(`http://198.199.67.201:8080/Api/Accounts/Agent/${id}`
    ,{ headers: {
      'Content-Type':'application/json',
            "Accept":"application/json",
            "Authorization":`Bearer ${jwt}`
    }}).catch(function (error) {
      if (error.response) {
        // Request made and server responded
        console.log(error.response.data);
        console.log(error.response.status);
        console.log(error.response.headers);
      } else if (error.request) {
        // The request was made but no response was received
        console.log(error.request);
      } else {
        // Something happened in setting up the request that triggered an Error
        console.log('Error', error.message);
      }
  
    });
    ;
    setUser(res.data);
    console.log(res)
    console.log(user.username)
  };
  return (
    <div className="container py-4">
      <Link className="btn btn-primary" to="/myagent">
        back to Home
      </Link>
      <h1 className="display-4">User Id: {id}</h1>
      <hr />
      <ul className="list-group w-50" class="ul border shadow">
      <li className="list-group-item">Name: {user.firstName}</li>
        <li className="list-group-item">LastName:{user.lastName}</li>
        <li className="list-group-item">Phone: {user.username}</li>
        <li className="list-group-item">LicenceNumber: {user.licenceNumber}</li>
        <li className="list-group-item">BusinessLNum: {user.businessLNum}</li>
        <li className="list-group-item">CompanyType: {user.companyType}</li>
        <li className="list-group-item">Account Locked: {user.credentialsNonExpired ? "true" : "false"}</li>
      </ul>
    </div>
  );
};

export default AgentView;
